body {
  height: 100vh;
  background-color: #e5e5e5;
  justify-content: center;
  align-items: center;
  margin: 0 50px;
  font-family: Georgia, serif;
  display: flex;
  overflow: hidden;
}

.app {
  width: 300px;
  height: 500px;
  background-color: #f1ffe1;
  border: 5px solid #abcea1;
  border-radius: 5px;
  padding: 15px;
}

#addNew {
  cursor: pointer;
  background-color: #abcea159;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  display: flex;
}

.fa-plus {
  background-color: #abcea1;
  border-radius: 3px;
  padding: 3px;
}

#msg {
  color: red;
}

#tasks {
  grid-template-columns: 1fr;
  gap: 15px;
  display: grid;
}

#tasks div {
  background-color: #e2eede;
  border: 3px solid #abcea1;
  border-radius: 5px;
  gap: 4px;
  padding: 5px;
  display: grid;
}

#tasks div .options {
  justify-self: center;
  gap: 20px;
  display: flex;
}

#tasks div .options i {
  cursor: pointer;
}

